body {
  margin: 20px;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

span {
  margin: 10px;
}

.temp-imperial {
  color: grey;
}

.city {
  display: grid;
  grid-template-columns: auto 1fr;
  /* background-color: lightgrey; */
  /* border-radius: 10px; */
  border-top: 1px black solid;
  /* text-align: center; */
}

.selected {
  background-color: lightgray;
}

.city img {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
}

.city h3 {
  grid-column: 2 / 3;
  grid-row: 1 / 1;
  margin-top: auto;
  margin-bottom: 0;
}

.city span {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  margin: 0;
}

.daily-forecast {
  display: grid;
  grid-template-columns: auto 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  align-items: center;
}

.daily-forecast img {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
}

.daily-forecast h2 {
  margin-bottom: 2px;
  grid-column: 2 / 4;
  grid-row: 1 / 2;
}

.daily-forecast span {
  margin-top: 2px;
  margin-bottom: auto;
  margin-left: 0;
  margin-right: 20px;
}

.current-temp {
  font-size: 2em;
}

.container-current-temp {
  display: flex;
  align-items: center;
}

h1 {
  padding-top: 0;
}

.selected-city {
  border-top: 3px black solid;
  border-bottom: 1px black solid;
  padding-bottom: 30px;
  margin-bottom: 10px;
  padding-top: 10px;
}

.selected-city-temp {
  display: grid;
  grid-template-columns: auto 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  align-items: center;
}

.selected-city-temp img {
  grid-column: 1/2;
  grid-row: 1/3;
}

.selected-city-temp .current-temp {
  grid-column: 2/3;
  grid-row: 1/2;
  margin-bottom: 0;
}

.selected-city-temp .current-temp.temp-imperial {
  grid-column: 3/4;
  grid-row: 1/2;
}

.selected-city-temp .temp-range {
  grid-column: 2/3;
  grid-row: 2/3;
}

.selected-city-temp .temp-range.temp-imperial {
  grid-column: 3/4;
  grid-row: 2/3;
}

button {
  font-size: 2em;
}

.App {
  display: grid;
  max-width: 1440px;
  margin: auto;
}

.city-selector {
  display: grid;
}

.forecast {
  display: grid;
}

@media (min-width: 1024px) {
  .city-selector {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .selected-city h1 {
    text-align: center;
  }

  .selected-city-temp img {
    width: 200px;
  }

  .selected-city-temp {
    grid-template-columns: 1fr 1fr 1fr;
    margin-left: auto;
    margin-right: auto;
    justify-items: center;
    width: 600px;
  }

  .selected-city-temp .current-temp {
    margin-top: auto;
  }

  .selected-city-temp .temp-range {
    margin-bottom: auto;
  }

  .forecast {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .daily-forecast {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto auto;
    justify-items: center;
  }

  .daily-forecast h2 {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }

  .daily-forecast img {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
  }

  .daily-forecast span {
    margin-top: 5px;
    margin-bottom: auto;
    margin-left: 0;
    margin-right: 20px;
  }
}
